import axios from "axios";

const logout = async () => {
  try {
    const logoutEndpoint = "http://localhost:5001/api/auth/logout";
    const response = await axios.post(logoutEndpoint);

    if (response.status === 200) {
      localStorage.removeItem("token");

      //window.location.href = "/SoYummy"; // Przekierowanie uytkownika na stron gwn po poprawnym wylogowaniu
    } else {
      throw new Error("Nie udao si wylogowa uytkownika.");
    }
  } catch (error) {
    if (error.response && error.response.status === 401) {
      console.error("Token JWT jest nieprawidowy lub wygas.");
      window.location.href = "/SoYummy";
      // Obsuga nieprawidowego lub wygasego tokenu JWT
    } else {
      console.error("Wystpi bd podczas wylogowywania:", error.message);
      // Obsuga innych bdw
    }
  }
};

export default logout;
